<template>
  <div class="modal">
    <div id="header">
      <img @click="closeToHome" class="logoKMenu" src="@/assets/images/K.svg" />
      <img @click="close" src="@/assets/images/MenuClose.svg" />
    </div>

    <div class="menu">
      <a class="rolling-text" v-on:click="goTo('Home')"> home </a>
      <br />
      <a class="rolling-text" v-on:click="goTo('Community')"> comunidade </a>
      <br />
      <a class="rolling-text" v-on:click="goTo('Career')"> carreira </a>
      <br />
      <a class="rolling-text" v-on:click="goTo('Shop')"> lojinha </a>
      <br />
      <a class="rolling-text" v-on:click="goTo('Newsletter')"> newsletter </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Menu',
  props: {
    isShowing: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    goTo(view) {
      this.$router.push({ name: view }).catch(() => {})
      this.close()
    },
    closeToHome() {
      this.$emit('close', 'home')
    },
    closeToNewsLetter() {
      this.$emit('close', 'newsletter')
    },
    close() {
      this.$emit('close', 'close')
    }
  },
  mounted() {
    document.body.classList.add('menu-toogle')
  },
  destroyed() {
    document.body.classList.remove('menu-toogle')
  }
}
</script>

